<!--  -->
<template>
  <div class="mod-config">
    <el-form
      :inline="true"
      :model="dataForm"
      @keyup.enter.native="getDataList()"
    >
      <el-form-item>
        <el-input
          v-model="dataForm.key"
          placeholder="参数名"
          clearable
        ></el-input>
      </el-form-item>
      <el-form-item>
        <el-select
          v-model="subjectType"
          placeholder="题目类型"
          @change="changeType(subjectType)"
        >
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          >
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button @click="getDataList()">查询</el-button>
        <el-button type="primary" @click="addOrUpdateHandle()">新增</el-button>
        <el-button
          type="danger"
          @click="deleteHandle()"
          :disabled="dataListSelections.length <= 0"
          >批量删除</el-button
        >
        <el-button @click="showAnwser()" type="success"
          >查看题目答案设置格式</el-button
        >
      </el-form-item>
    </el-form>
    <el-table
      :data="dataList"
      border
      v-loading="dataListLoading"
      @selection-change="selectionChangeHandle"
      style="width: 100%"
    >
      <el-table-column
        type="selection"
        header-align="center"
        align="center"
        width="50"
      >
      </el-table-column>
      <el-table-column
        prop="subjectName"
        header-align="center"
        align="center"
        label="题目描述"
      >
      </el-table-column>
      <el-table-column
        prop="aDescribtion"
        header-align="center"
        align="center"
        label="A选项描述"
      >
      </el-table-column>
      <el-table-column
        prop="bDescribtion"
        header-align="center"
        align="center"
        label="B选项描述"
      >
      </el-table-column>
      <el-table-column
        prop="cDescribtion"
        header-align="center"
        align="center"
        label="C选项描述"
      >
      </el-table-column>
      <el-table-column
        prop="dDescribtion"
        header-align="center"
        align="center"
        label="D选项描述"
      >
      </el-table-column>
      <el-table-column
        prop="subjectImage"
        header-align="center"
        align="center"
        label="题目图片"
      >
        <template slot-scope="scope">
          <img
            :src="scope.row.subjectImage"
            style="width: 100px; height: 100px"
          />
        </template>
      </el-table-column>

      <el-table-column
        prop="subjectType"
        label="题目类型"
        width="100"
        filter-placement="bottom-end"
      >
        <template slot-scope="scope">
          <el-tag disable-transitions>{{ scope.row.subjectType }}</el-tag>
        </template>
      </el-table-column>

      <el-table-column
        prop="subjectAnwser"
        header-align="center"
        align="center"
        label="题目答案"
      >
      </el-table-column>

      <el-table-column
        prop="score"
        header-align="center"
        align="center"
        label="题目分数"
      >
      </el-table-column>
      <el-table-column
        fixed="right"
        header-align="center"
        align="center"
        width="150"
        label="操作"
      >
        <template slot-scope="scope">
          <el-button
            type="text"
            size="small"
            @click="addOrUpdateHandle(scope.row.id)"
            >修改</el-button
          >
          <el-button
            type="text"
            size="small"
            @click="deleteHandle(scope.row.id)"
            >删除</el-button
          >
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      @size-change="sizeChangeHandle"
      @current-change="currentChangeHandle"
      :current-page="pageIndex"
      :page-sizes="[10, 20, 50, 100]"
      :page-size="pageSize"
      :total="totalPage"
      layout="total, sizes, prev, pager, next, jumper"
    >
    </el-pagination>
    <add-or-update
      v-if="addOrUpdateVisible"
      ref="addOrUpdate"
      @refreshDataList="getDataList"
    ></add-or-update>

    <el-dialog
      title="提示"
      :visible.sync="dialogVisible"
      width="30%"
   
    >
      <span class="showAnwser">
        对于单选题，直接输入答案 例如：A<br />
        对于多选题，直接输入答案 例如：AB(中间无任何符号隔开)<br />
        对于填空题，直接输入答案 例如：1/2<br />
        对于判断题，正确输入：√ 错误输入：× (请直接复制符号)<br />
        对于大题，需要老师判断后给予学生得分，请直接输入：略
      </span>
      <span slot="footer" class="dialog-footer"> </span>
    </el-dialog>
  </div>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';
import AddOrUpdate from "./subject-add-or-update";
export default {
  //import引入的组件需要注入到对象中才能使用
  components: {
    AddOrUpdate,
  },
  data() {
    //这里存放数据
    return {
      dataForm: {
        key: "",
      },
      dataList: [],
      pageIndex: 1,
      pageSize: 10,
      totalPage: 0,
      dataListLoading: false,
      dataListSelections: [],
      addOrUpdateVisible: false,
      dialogVisible: false,
      token: "",
      options: [
        {
          value: 6,
          label: "全部",
        },
        {
          value: 0,
          label: "单选题",
        },
        {
          value: 1,
          label: "多选题",
        },
        {
          value: 3,
          label: "判断题",
        },
        {
          value: 2,
          label: "填空题",
        },
        {
          value: 4,
          label: "简答题",
        },
      ],
      subjectType: 6,
    };
  },
  //监听属性 类似于data概念
  computed: {},
  //监控data中的数据变化
  watch: {},
  //方法集合
  methods: {
    //发生改变的时候
    changeType(subjectType) {
      console.log(subjectType);
      this.getDataList();
    },
    // 删除
    deleteHandle(id) {
      var ids = id
        ? [id]
        : this.dataListSelections.map((item) => {
            return item.id;
          });
      this.$confirm(
        `确定对[id=${ids.join(",")}]进行[${id ? "删除" : "批量删除"}]操作?`,
        "提示",
        {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning",
        }
      ).then(() => {
        this.$http
          .post(
            "courseSystem/subject/delete",
            { ids },
            {
              headers: {
                token: this.token,
              },
            }
          )
          .then(({ data }) => {
            if (data && data.code === 0) {
              this.$message({
                message: "操作成功",
                type: "success",
                duration: 1500,
                onClose: () => {
                  this.getDataList();
                },
              });
            } else {
              this.$message.error(data.msg);
            }
          });
      });
    },

    //查看题目答案格式
    showAnwser() {
      this.dialogVisible = true;
    },
    // 获取数据列表
    getDataList() {
      this.dataListLoading = true;
      this.$http
        .get("courseSystem/subject/list", {
          params: {
            page: this.pageIndex,
            pageSize: this.pageSize,
            key: this.dataForm.key,
            subjectType: this.subjectType,
          },
          headers: {
            token: this.token,
          },
        })
        .then(({ data }) => {
          if (data && data.code === 0) {
            this.dataList = data.page.list;
            this.dataList.map((item) => {
              if (item.subjectType == 1) {
                item.subjectType = "多选题";
              } else if (item.subjectType == 0) {
                item.subjectType = "单选题";
              } else if (item.subjectType == 2) {
                item.subjectType = "填空题";
              } else if (item.subjectType == 3) {
                item.subjectType = "判断题";
              } else if (item.subjectType == 4) {
                item.subjectType = "简答题";
              }
            });
            this.totalPage = data.page.totalCount;
          } else {
            this.dataList = [];
            this.totalPage = 0;
          }
          this.dataListLoading = false;
        });
    },
    // 每页数
    sizeChangeHandle(val) {
      this.pageSize = val;
      this.pageIndex = 1;
      this.getDataList();
    },
    // 当前页
    currentChangeHandle(val) {
      this.pageIndex = val;
      this.getDataList();
    },
    // 多选
    selectionChangeHandle(val) {
      this.dataListSelections = val;
    },
    // 新增 / 修改
    addOrUpdateHandle(id) {
      this.addOrUpdateVisible = true;
      this.$nextTick(() => {
        this.$refs.addOrUpdate.init(id);
      });
    },
  },
  //生命周期 - 创建完成（可以访问当前this实例）
  created() {
    this.token = this.$store.state.user.token;
    console.log(this.token);
    this.getDataList();
  },
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted() {},
  beforeCreate() {}, //生命周期 - 创建之前
  beforeMount() {}, //生命周期 - 挂载之前
  beforeUpdate() {}, //生命周期 - 更新之前
  updated() {}, //生命周期 - 更新之后
  beforeDestroy() {}, //生命周期 - 销毁之前
  destroyed() {}, //生命周期 - 销毁完成
  activated() {}, //如果页面有keep-alive缓存功能，这个函数会触发
};
</script>
<style scoped>
.showAnwser {
  font-weight: bold;
  font-size: 20px;
}
</style>